body {
    font-family: 'Josefin Slab', serif;
}

.container {
    font-family: 'Cabin', sans-serif;
    font-size: 1.2rem;

}
.right-pulled::before {
    right: -1.5rem;
    left: inherit;
}

.right-pulled::after {
    right: -1.5rem;
    left: inherit ;
}

.custom-control {
    align-items: center;
  }

  .custom-control-label {
    cursor: pointer;
  }

.custom-control-label::after,
.custom-control-label::before {
    top: 0.35rem;
}

  .custom-control-label.small {
    vertical-align: middle;
  }

  .custom-control-right {
    position: relative;
    display: block;
    min-height: 1.5rem;
    padding-right: 1.5rem;  
}

.custom-control-label-reverted {
    position: relative;
    margin-bottom: 0;
    cursor: pointer;
}

  .custom-control-label-reverted::after {
    position: absolute;
    top: .25rem;
    display: inline-block;
    width: 1rem;
    height: 1rem;
    pointer-events: none;
    content: "";
    background-color: #fff;
    border: #adb5bd solid 1px;
    border-radius: .25rem;
    transition: background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 57% 57%;
    vertical-align: top;
}

.custom-control-label.small::after,
.custom-control-label.small::before,
.custom-control-label-reverted.small::after {
    top: 0.25rem;
}

.custom-checkbox .custom-control-input:checked~.custom-control-label-reverted::after {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
    color: #fff;
    border-color: #007bff;
    background-color: #007bff; 
}

.custom-control-input:focus:not(:checked)~.custom-control-label-reverted::after {
    border-color: #80bdff;
}
.custom-control-input:focus~.custom-control-label-reverted::after {
    box-shadow: 0 0 0 0.2rem rgba(0,123,255,.25);
}

.custom-control-label-reverted::before {
    position: absolute; 
    top: .25rem;
    display: inline-block;
    width: 1rem;
    height: 1rem;
    content: "";
    vertical-align: top;
}

.row:hover div.hover {
    background-color: rgba(0,0,0,.075);
}